#intro {
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;

    /*set height shrink*/
    height: 80vh;
    margin-top: 10vh;
    border-radius: 50px;
}

.introContent{
    height: 100vh;
    width: 100vh;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-size: 1.75rem;
    font-weight: 200;
}

.introName{
    color: yellow;
}

.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
    width: 472px;
    text-align: left;
}

.btn{
    background-color: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
    cursor: pointer;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
    cursor: pointer;
}

/*mobile view*/
@media screen and (max-width:840px) {

    .bg{
        right: -10vw;
    }

    .introContent{
        font-size: 10vw;
    }

    .hello{
        font-size: 4.5vw;
    }
    
}

/*diffrent phone view*/
@media screen and (max-width:480px) {

    .bg{
        height: 35vh;
        width: 70vw;
        left: 15vw;
    }

    
}